.FPs {
  display: inline-flex;
  height: auto;
  color: inherit;
}
.FPs:hover,
.newMusicStyle:hover {
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
  filter: drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.8));
  cursor: pointer;
}

.FPs {
  list-style-type: none;
  margin-top: -1vh;
  margin-bottom: -1vh;
}
.FPs li {
  position: relative;
  filter: drop-shadow(0px 0px 4px rgba(255, 255, 255, 0.8));
}

.FP p {
  position: absolute;
  top: 7vh;
  text-align: center;
  font-weight: bold;
  font-size: 1.2vw;
  width: 12vw;
  text-shadow: 10px rgb(0, 0, 0, 1);
  vertical-align: middle;
  color: inherit;
}

.FPs img {
  height: 12vw;
  outline: 1px solid white;
  border-radius: 0.9vw;
  outline-offset: -2px;
  display: block;
  width: 12vw;
}

.ImageFilter {
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 0.9vw;
}
.featuredPlaylistsDB {
  width: 95%;
  margin-left: 2vw;
  position: relative;
}
.FPs:first-child {
  margin-left: 0px;
}
.loading {
  align-items: center;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  height: 13vw;
  width: 75vw;
  top: 0vw;
  border-radius: 25px;
}
.loading svg {
  position: relative;
  top: 4vw;
  left: 36vw;
}
