.MainWindow {
  width: 80vw;
  color: inherit;
  display: block;
  margin-left: 20vw;
  max-height: 100vh;
}
.Text {
  margin-left: 1vw;
  padding-top: 2vh;
  margin-bottom: 2vh;
  font-size: 3vh;
  font-weight: 700;
}

ul {
  list-style: none;
  padding-top: 20px;
  margin-left: -20px;
  font-size: 3vh;
}

ul li::before {
  font-weight: bold;
  display: inline-block;
}

.App {
  background-color: transparent;
  font-size: var(--font-size-lg);
  color: var(--color-white);
  font-family: var(--font-montserrat);
  color: inherit;
}

.rectangle {
  position: fixed;
  height: 2.083vw;
  width: 0.781vw;
  background-color: rgb(255, 255, 255);
  margin-left: -20vw;
  margin-top: 6.2vh;
  filter: drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.8));
  z-index: 10;
}
